.video {
  position: relative;
  overflow: hidden;

  &__poster {
    width: 100%;
  }

  &__player {
    position: absolute;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.3s;
    
    &:hover {
      background-color: rgba(0, 0, 0, 0.3);
    }
  }

  &__play {
    &-icon {
      position: absolute;
      height: 24px;
      width: 24px;
      background-image: url(../images/icon/play.svg);
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      opacity: 0;
      transition: opacity 0.4s;

      @include onDesktop {
        height: 35px;
        width: 35px;
      }
    }

    &-text {
      font-family: "DIN Pro", sans-serif;
      font-weight: 500;
      font-size: 14px;
      line-height: 18px;
      color: #f3f4f5;
      text-transform: uppercase;
      transition: opacity 0.4s;

      @include onTablet {
        font-size: 18px;
      }

      @include onDesktop {
        font-size: 24px;
      }
    }
  }

  &__button {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80px;
    height: 80px;
    border: 1px solid #f3f4f5;
    border-radius: 50%;
    cursor: pointer;

    @include onTablet {
      width: 150px;
      height: 150px;
    }

    @include onDesktop {
      width: 200px;
      height: 200px;
    }

    &:hover > .video__play-icon {
      opacity: 1;
    }

    &:hover > .video__play-text {
      opacity: 0;
    }
  }

  &__full-width {
    width: 100%;
    display: block;
  }

}
